<template>
  <v-container>
    <v-stepper  v-model="step">
      <v-stepper-header >
        <v-stepper-step :color="color" :complete="step > 1" step="1">
          Выберите изображения
        </v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :color="color" :complete="step > 2" step="2">
          Отредактируйте
        </v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step :color="color" step="3">
          Добавьте описание
        </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card class="mb-12 pa-5" color="grey lighten-5" >
            <v-file-input
                v-model="imagesInput"
                dense
                @change="handleFiles()"
                outlined
                :color="color"
                counter
                multiple
                placeholder="Выберите изображения"
                accept="image/png, image/jpeg, image/bmp, image/jpg"
                show-size
                label="Изображения"
            ></v-file-input>
            <v-card-text v-if="images.length>0" class="pt-0">Изображения</v-card-text>
            <v-row>
              <v-col
                  v-for="(image, index) in images"
                  :key="index"
                  class="d-flex child-flex"
                  cols="4"
              >
                <v-img :src="image" aspect-ratio="1" class="grey lighten-2">
                  <template v-slot:placeholder>
                    <v-row class="fill-height ma-0" align="center" justify="center">
                      <v-progress-circular
                          indeterminate
                          color="grey lighten-5"
                      ></v-progress-circular>
                    </v-row>
                  </template>
                </v-img>
              </v-col>
            </v-row>
          </v-card>

          <v-btn :disabled="images.length<=0" :color="color" @click="step = 2" dark>
            Продолжить
          </v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card class="mb-12" color="grey lighten-1" height="200px">
            <v-card-text class="white--text">Неактивный раздел</v-card-text>
          </v-card>

          <v-btn :color="color" @click="step = 3" dark>
            Продолжить
          </v-btn>

          <v-btn class="ml-2" text @click="step = 1">
            Назад
          </v-btn>
        </v-stepper-content>

        <v-stepper-content step="3">
          <v-card class="mb-12" color="grey lighten-1" height="400px">
            <v-card-text class="white--text">Неактивный раздел</v-card-text>
          </v-card>
          <v-btn :color="color" dark @click="step = 1; sendImages()">
            Готово
          </v-btn>
          <v-btn class="ml-2" text @click="step = 2">
            Назад
          </v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </v-container>
</template>

<script>

export default {
  name: "Upload",
  data () {
    return {
      imagesInput: [],
      images: [],
      step: 1,
    }
  },
  methods:{
    sendImages(){
      this.$store.dispatch('SEND_IMAGES', this.images).then(()=>{
        setTimeout(()=>{
          this.$router.push('/')
        },3000)
      })
    },
    handleFiles(){
      let files = []
      this.imagesInput.forEach(file=>{
        let reader = new FileReader()
        reader.onload = function(e) {
          files.push(e.target.result)
        }
        reader.readAsDataURL(file)
      })
      this.images =  files
    }
  },
  computed:{
    color () {
      return this.$store.getters.getActiveColor
    },
  }
}

</script>

<style scoped>
/deep/.theme--dark.v-btn.v-btn--disabled {
  color: #a8a8a8 !important ;
}
</style>